.company_info_main {
    width: 100%;
    height:100vh;

    display: grid;
    grid-template-rows: 60px auto;

    /* background-color: red; */
    overflow-y: scroll;
    padding-bottom: 5vh;
}

.company_info_main > div {
    grid-row: 1 / 2;

    width: 90%;
    height: 100%;

    padding: 0 5%;

    margin-top: 0px;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.go_back_icon {
    font-size: 1.4rem;
    font-weight: lighter;
    color: #fff;
    cursor: pointer;
}

.company_info_main > section {
    grid-row: 2 / -1;
    height: auto;
    width: 100%;
    color: #fff;
    padding-bottom: 5vh;
    padding-top: 0;
}

@media (max-width: 1024px) {
    .company_info_main {
        width: 100%;
        grid-template-rows: 8vh auto;
    }

    .company_info_main > section {
        padding-bottom: 20vh;
    }
}
